.sidebar {
  padding: 10px 0;
  :global(.iconfont) {
    margin-right: 10px;
  }
}

.header {
  padding: 0 20px;
  :global(.iconfont) {
    color: #FFF;
    cursor: pointer;
  }
  .avatar{
    float: right;
    margin: 7px 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
  }
}

.content {
  padding: 20px;
  min-height: 1000px !important;
}

.g2-view-container {
  margin: auto;
}
